.navbar {
    display: flex;
    justify-content: space-between; /* Change justify-content to space-between */
    align-items: center;
    background-color: rgb(54, 43, 43);
    padding: 10px;
    margin-left: 0; /* Remove margin-left */
    margin-right: 0; /* Remove margin-right */
    width: 100%; /* Add width: 100% to make it full size */

}
  
  .logo {
    font-family: Cedarville Cursive;
    font-size: 24px;
    font-weight: bold;
    color: white;
 
    margin-left: 7em;
 
    /* background-color: rgba(26, 3, 3, 0.486); */
    padding: 5px 10px;
  }
  
  .menu {
    display: flex;
    gap: 10px;
    margin-right: 10em;

  }
  
  
  .menu a {
    text-decoration: none;
    color: white;
    /* background-color: gray; */
    padding: 5px 10px;
  }
  
  .menu a:hover {
    text-decoration: none;
    text-decoration-line: underline;
    text-decoration-color: white;
    text-decoration-thickness: 2px;
    text-underline-offset: 5px;
  }
  .menu a.active {
    padding: 2px;
    text-decoration: underline;
  }